.container {
  @include media-breakpoint-up(lg) {
    padding-left: 72px;
    padding-right: 72px;
  }
}

// btn
.moreBtn {
  padding: 8px 42px;
  font-size: $h5-font-size; //18px
  font-family: "squada One";
  width: 140px;
  position: relative;
  border: 1px solid black;
  &:after {
    // 
    content: "";
    position: absolute;
    right: 0;
    bottom: 0px;
    border-left: 16px transparent solid;
    border-bottom: 16px #e6553b solid;
  }
  &:hover {
    &:after {
      border-bottom: 16px black solid;
    }
  }
}

// hover
.btn-white {
  background-color: white;

  &:hover {
    background-color: #e6553b;
    color: white;
  }
}

// btn hover 
.moreBtn-border {
  padding: 8px 42px;
  font-size: $h5-font-size; //18px
  font-family: "squada One";
  width: 140px;
  position: relative;
  border: 1px solid transparent;
  &:after {
    // 
    content: "";
    position: absolute;
    right: 0;
    bottom: -2px;
    border-left: 16px transparent solid;
    border-bottom: 16px #e6553b solid;
  }
  &:hover {
    background-color: #e6553b;
    color: white;
    border-top: 1px solid black;
    border-right: 1px solid black;
    border-left: 1px solid black;
    &:after {
      border-bottom: 16px black solid;
    }
  }
}

