//  hover
// 
.triangle {
  position: absolute;
  bottom: 0;
  right: 0;

  /*    */
  width: 0px;
  height: 0px;
  border-right: 10px solid #e6553b;
  border-top: 10px solid transparent;
  display: none;
}
// 
.img-hover {
  position: relative;
  background-color: #000;
  &:hover {
    // more
    &::after {
      content: "MORE";
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      background-color: #000;
      opacity: 0.5;
      color: #fff;
      display: flex;
      justify-content: center;
      align-items: center;
    }
    .triangle {
      display: block;
      z-index: 1;
    }
  }
}

// 
.img-hover-md {
  position: relative;
  background-color: #000;
  &:hover {
    &::after {
      content: "";
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      left: 0;
      background-color: #000;
      opacity: 0.5;
      color: #fff;
      display: flex;
      justify-content: center;
      align-items: center;
    }
    .moreBtn {
      display: block;
      z-index: 1;
    }
  }
}
